






























































import { Component, Vue } from 'vue-property-decorator';
import { getTopicDetail } from '@/api';
import { TopicDetail } from '@/interface';
import MarkDown from '@/components/MarkDown.vue';
import ReplyList from './ReplyList.vue';

@Component({
  components: {
    MarkDown,
    ReplyList,
  },
})
export default class TopicDetailPage extends Vue {
  private topicDetail: TopicDetail | null = null;
  private dialog: boolean = false;
  created() {
    this.getDetail();
    this.dialog = this.$route.meta.reply || false;
  }
  activated() {
    this.getDetail();
    this.dialog = this.$route.meta.reply || false;
  }
  deactivated() {
    this.topicDetail = null;
    this.dialog = false;
  }
  private async getDetail() {
    const id = this.$route.params.id;
    const data = await getTopicDetail(id);
    if (data) {
      this.topicDetail = data;
    }
  }
}
